var that;
export default {
	data() {
		return {  
			list: [], 
			page: 1,
			nolist: false,
			nomore: false,
			lodstatus: ''
		};
	},
	methods: { 
		getlist(i) {
			if (that.nomore) {
				return;
			}
			that.lodstatus = 'loading';
			that.nolist = false;
			this.$request
				.request({
					url: that.api.share.get_money_log,
					data: { 
						page: that.page
					}
				})
				.then(res => {
					console.log(res);
					that.lodstatus = '';
					if (res.length > 0) {
						that.list = that.list.concat(res);
						that.page = that.page + 1;
						that.lodstatus = 'more';
					} else {
						if (that.page != 1) {
							that.nomore = true;
							that.lodstatus = 'noMore';
						} else {
							that.nolist = true;
						}
					}
				 
					uni.setStorageSync('zjmx', {
						page: that.page,
						list: that.list
					});
			 
				});
		}
	},
	onLoad() {
		that = this; 
		let data=uni.getStorageSync('zjmx');
		that.list=data.list;
		that.page=data.page; 
		that.getlist();
	},
	onReachBottom() {
		that.getlist();
	},
	onPullDownRefresh() {
		that.list = [];
		that.page = 1;
		that.getlist(1);
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	}
};
